const Downcoder = {
    Initialize: function () {
        if (Downcoder.map) {
            // already made
            return;
        }
        Downcoder.map = {};
        Downcoder.chars = [];
        for (let i = 0; i < ALL_DOWNCODE_MAPS.length; i++) {
            const lookup = ALL_DOWNCODE_MAPS[i];
            for (let c in lookup) {
                if (lookup.hasOwnProperty(c)) {
                    Downcoder.map[c] = lookup[c];
                }
            }
        }
        for (let k in Downcoder.map) {
            if (Downcoder.map.hasOwnProperty(k)) {
                Downcoder.chars.push(k);
            }
        }
        Downcoder.regex = new RegExp(Downcoder.chars.join("|"), "g");
    }
};

function downcode(slug) {
    Downcoder.Initialize();
    return slug.replace(Downcoder.regex, m => {
        return Downcoder.map[m];
    });
}

export default function urlify(s, num_chars, allowUnicode) {
    // changes, e.g., "Petty theft" to "petty-theft"
    // remove all these words from the string before urlifying
    if (!allowUnicode) {
        s = downcode(s);
    }
    const hasUnicodeChars = /[^\u0000-\u007f]/.test(s);
    // Remove English words only if the string contains ASCII (English)
    // characters.
    if (!hasUnicodeChars) {
        const removeList = [
            "a",
            "an",
            "as",
            "at",
            "before",
            "but",
            "by",
            "for",
            "from",
            "is",
            "in",
            "into",
            "like",
            "of",
            "off",
            "on",
            "onto",
            "per",
            "since",
            "than",
            "the",
            "this",
            "that",
            "to",
            "up",
            "via",
            "with"
        ];
        const r = new RegExp("\\b(" + removeList.join("|") + ")\\b", "gi");
        s = s.replace(r, "");
    }
    // if downcode doesn't hit, the char will be stripped here
    if (allowUnicode) {
        // Keep Unicode letters including both lowercase and uppercase
        // characters, whitespace, and dash; remove other characters.
        s = XRegExp.replace(s, XRegExp("[^-_\\p{L}\\p{N}\\s]", "g"), "");
    } else {
        s = s.replace(/[^-\w\s]/g, ""); // remove unneeded chars
    }
    s = s.replace(/^\s+|\s+$/g, ""); // trim leading/trailing spaces
    s = s.replace(/[-\s]+/g, "-"); // convert spaces to hyphens
    s = s.substring(0, num_chars); // trim to first num_chars chars
    s = s.replace(/-+$/g, ""); // trim any trailing hyphens
    return s.toLowerCase(); // convert to lowercase
}
