/**
 *        .
 *    JS-.
 * @returns {String[]}
 */
export default function getPossibleLocales() {
    const locales = [];

    let locale = document.documentElement.lang;
    if (locale) {
        //     
        locale = locale.toLowerCase();
        locales.push(locale);

        if (locale.includes("-")) {
            // ,      
            locale = locale.replace("-", "_");
            locales.push(locale);
        }

        if (locale.includes("_")) {
            //   
            locale = locale.replace(/_.*$/, "");
            locales.push(locale);
        }
    }

    //  -
    if (!locales.includes("en")) {
        locales.push("en");
    }

    return locales;
}
